.trip{
    margin:4rem 6rem;
    color:#2a2a2a;
}
.trip h1{
    font-size:3rem;
    text-align: center;
}
.trip p{
    text-align: center;
}
.trip-data{
    margin-top:3rem;
    display:flex;
    justify-content: space-between;
}
.trip-card{
    width: 32%;
    text-align:start;
    box-shadow:0 5px 25px 2px rgba(0,0,0,0.11);
    border-radius: 7px;
    cursor: pointer;
    padding :1rem .8rem;
}
.trip-image{
    height:200px;
    overflow:hidden;
    border-radius: 7px;
}
.trip-image img{
    width:100%;
    height:100%;
    transition: 0.3s ease-in-out;
    border-radius:10px;
}
.trip-image:hover img{
    transform:scale(1.3);
}
.trip-card h4{
    font-size: 1.3rem;
    padding:.9rem 0 .2rem 0;
    
}
.trip-card p{
    text-align: start;
}

@media screen and (max-width:850px){
    .trip{
        margin:4rem 2rem ;
    }
    .trip-data{
       
        flex-direction: column;
    }
    .trip-card{
        width:100%;
        margin-bottom:.1.5rem;
    }
}